/* end breakpoints */
/* variable */
/* end variable */
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

p {
  font-family: "Source Sans Pro", sans-serif;
}

h1, h2, h3, h4, h5 {
  font-family: "Dosis", sans-serif;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
  margin-right: 15px;
  width: 100%;
}

.block__text {
  max-width: 1200px;
}

/*================== nav ==================*/
.logo img {
  width: 160px;
  padding: 15px;
  margin: 0 20px;
}

.logo {
  position: absolute;
}

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}

.change .bar1 {
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  transform: rotate(45deg) translate(-8px, -8px);
}

.menu {
  list-style-type: none;
  width: 100%;
  z-index: 1;
  position: fixed;
  min-height: 50px;
  top: 0;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .menu {
    width: 95%;
  }
}

.menu__nav {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  align-items: center;
  color: #333333;
  padding-top: 50px;
  width: 100%;
  padding-left: 0;
}

.menu__nav li {
  padding: 10px 0;
  display: inline-block;
  z-index: 1;
}

.menu__nav li a {
  text-decoration: none;
  color: #333333;
  font-family: "Dosis", sans-serif;
}

.menu__nav li a.active {
  color: #337ab7;
}

.menu__nav-bar {
  float: right;
  margin: 10px 20px;
}

.is-close {
  display: none;
}

@media (min-width: 768px) {
  .menu__nav {
    flex-direction: row;
    width: 100%;
    justify-content: flex-end;
    padding-top: 0;
  }
  li {
    margin: 0 20px;
  }
  li a {
    text-decoration: none;
    color: #333333;
  }
  .menu__nav-bar {
    display: none;
  }
  .is-close {
    display: inline-flex;
  }
}

/* contact.html */
.content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* hero */
.hero {
  padding-top: 150px;
}

/* form */
.form-content {
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: center;
}

.form {
  width: 100%;
}

input, textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #E5E5E5;
  color: #999999;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 8px;
  -moz-box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 8px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 8px;
}

@media (min-width: 768px) {
  input, textarea {
    width: 400px;
  }
  .form {
    width: auto;
  }
}

textarea {
  height: 150px;
  line-height: 18px;
}

input:hover, textarea:hover,
input:focus, textarea:focus {
  border-color: 1px solid #C9C9C9;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 8px;
  -moz-box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 8px;
  -webkit-box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 8px;
}

.form label {
  margin-left: 10px;
  color: #999999;
}

/* ===========================
   ====== Submit Button ======
   =========================== */
.submit input {
  width: 100px;
  height: 40px;
  background-color: #474E69;
  color: #FFF;
  border-radius: 3px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
}

/* end form */
/* about */
.about {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

/* end about */
/* end contact.html */
/* start about.html */
.photo__section {
  display: block;
  margin: 0 15px;
}

.photo__section-image {
  width: 100%;
  height: auto;
}

.photo__section-bottom {
  margin: 0 15px;
}

@media (min-width: 768px) {
  .photo__section {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
  }
  .photo__section-left {
    flex-direction: row;
    display: flex;
    flex: 1 50%;
    margin: 0 15px;
    max-height: 750px;
  }
  .photo__section-right {
    flex-direction: column;
    display: flex;
    flex: 1 50%;
    margin: 0 15px;
  }
  .text-photo__section {
    display: flex;
    justify-content: center;
  }
  .photo__section-image {
    max-width: 100%;
  }
  .photo__section-bottom {
    max-width: 1200px;
    margin: 0 auto;
  }
}

/* end about.html */
